html {
    font-size: 10px;
}

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    background-color: rgb(255, 255, 255);
    color: #333;
}

p {
    font-size: 1.6rem;
}

small {
    font-size: 1.2rem;
}

.container {
    background-image: linear-gradient(to right, #ffafbd, #ffc3a0);
    margin: 0 auto;
    max-width: 1000px;
    padding: 40px;
}

.header {
    background-image: linear-gradient(to right, #ffafbd, #ffc3a0);
}

.title {
    font-size: 4.4rem;
    text-align: center
}

.form {
    display: grid;
}

.label {
    font-size: 1.2rem;
    margin-bottom: 0.2rem;
    text-transform: uppercase;
}

.input {
    font-size: 1.6rem;
    padding: 0.5rem 2rem;
    line-height: 2.8rem;
    border-radius: 20px;
    border: 1px solid #ddd;
    margin-bottom: 1rem;
}

.button {
    background-color: rgba(0, 0, 0, 0.75);
    color: white;
    padding: 1rem 2rem;
    border: 1px solid rgba(0, 0, 0, 0.75);
    border-radius: 20px;
    font-size: 1.4rem;
    cursor: pointer;
}

button:disabled {
  border: 1px solid #999999;
  background-color: #cccccc;
  color: #666666;
  cursor: not-allowed;
}

@media (min-width:786px) {
    .form {
        grid-template-columns: 1fr auto;
        grid-gap: 1rem;
        align-items: center;
    }
    .input {
        margin-bottom: 0;
    }
}

.movie-card-list {
    margin-top: 4rem;
}

.movie-card {
    display: grid;
    grid-template-columns: auto 1fr;
    vertical-align: top;
    padding: 2rem 4rem;
    border-radius: 10px;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.25);
    margin-bottom: 2rem;
    background-color: white;
}

.movie-card-content {
    vertical-align: top;
    margin-left: 20px;
}

.movie-card-title {
    margin-bottom: 0.5rem;
    font-size: 3.2rem;
    margin-block-start: 0em !important;
}

.movie-card-image {
    margin: 0 auto;
    display: block
}

.message {
    text-align: center;
}

.loader {
    text-align: center;
}

.date {
    color: #999;
}

.button-next {
    text-align: right;
}

.next{
    text-align: center;
    font-style: italic;
    padding: 10px;
    cursor: pointer;
    font-size: 2.5rem;
}